.modal_overlay{
    height: 100vh;
    width: 100vw;
    background: var(--background-modal-overlay);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
    overflow: auto;
    z-index: 999;
}

.modal_overlay_animate{
    overflow: hidden;
}