.ingredient_card {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  cursor: move;
}
.ingredient_count {
  position: absolute;
  right: 0px;
  top: 0px;
}

.ingredient_image {
  display: block;
  width: 240px;
  height: 120px;
}

.ingredient_price_container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px;
}
.ingredient_price_icon svg {
  height: 22px;
  margin-left: 9px;
}
.ingredient_name {
  padding: 0px;
  margin: 0px;
  text-align: center;
}
